import { CqsSavedQuery } from "@rp/common/api-types"
import { generateDate } from "./generateDate"
import { generateId } from "./generateId"
import { userOwner } from "./users"

type Params = {
  name: string
  saveStatus?: "saved" | "unsaved"
}

export const generateQuery = ({ name, saveStatus = "saved" }: Params): CqsSavedQuery => {
  return {
    id: generateId(),
    name: name,
    query: {},
    saveStatus,
    ownerUserId: userOwner.ownerUserId,
    ownerName: userOwner.ownerName,
    dateCreated: generateDate(),
    lastUpdated: generateDate(),
    lastExecuted: null,
    shared: {
      company: { organizationId: userOwner.organization.id, permission: "undefined" },
      users: [],
    },
  }
}
